import ExportButton from "./table/button";
import PageBlocker from "./table/pageblocker";
import {Exporter, EXPORTER_EVENTS} from "./table/exporter";
import ScreenOption from "./table/screen-option";

let nanobus = require( 'nanobus' );

document.addEventListener( 'DOMContentLoaded', () => {
	let button = document.querySelector( '.ac-table-button.-export' );
	AdminColumns.Export = {
		Exporter : new Export( button ),
	};

	let elScreenOption = document.querySelector( '#acp_export_show_export_button' );
	if ( elScreenOption ) {
		AdminColumns.Export.ScreenOption = new ScreenOption( elScreenOption );
		AdminColumns.Export.ScreenOption.init();
	}
} );

class Export {

	constructor( button ) {
		if ( !button ) {
			return;
		}

		this.events = nanobus();
		this._button = new ExportButton( button );
		this._init();
	}

	_init() {
		this._button.events.on( 'export', () => {
			this.export();
		} );

		this.events.on( 'completed', () => {
			PageBlocker.disable();
			this._button.enable();
		} )
	}

	export() {
		PageBlocker.enable();

		// Initiate Exporter
		let exporter = new Exporter();

		exporter.events.on( EXPORTER_EVENTS.COMPLETED, () => {
			this.events.emit( 'completed' );
		} );

		exporter.start();
	}

}