import {guidGenerator} from "../helper/string";
import Message from "./message";

const nanobus = require( 'nanobus' );

export const EXPORTER_EVENTS = {
	COMPLETED : 'completed'
};

export class Exporter {

	constructor() {
		this.events = nanobus();
		this.message = new Message();
		this.counter = 0;
		this.hash = guidGenerator();
	}

	start() {
		this.message.render();
		this.counter = 0;
		this.run();
	}

	continue() {
		this.counter++;
		this.run();
	}

	run() {
		this.call().success( result => {
			if ( !result.success ) {
				let errorMsg = result.data ? result.data : ACP_Export.i18n.export_error;

				this.message.setError( errorMsg );
				this.message.makeDismissible();
				this.events.emit( EXPORTER_EVENTS.COMPLETED );
				return;
			}

			if ( result.data[ 'num_rows_processed' ] > 0 ) {
				this.message.addProcessed( result.data[ 'num_rows_processed' ] );
				this.continue();
			} else {
				this.complete( result.data )
			}
		} );
	}

	complete( result ) {
		this.events.emit( EXPORTER_EVENTS.COMPLETED );
		this.message.setDownloadUrl( result[ 'download_url' ] );
		this.message.complete();
		window.location.href = result[ 'download_url' ];
	}

	call() {
		const data = {
			_wpnonce : ACP_Export.nonce,
			acp_export_action : 'acp_export_listscreen_export',
			acp_export_hash : this.hash,
			acp_export_counter : this.counter
		};

		return jQuery.ajax( {
			method : 'get',
			url : window.location.href,
			data,
		} );
	}
}