/**
 * @return {string}
 */
const guidGenerator = () => {
	/**
	 * @return {string}
	 */
	let S4 = function() {
		return (((1 + Math.random()) * 0x10000) | 0).toString( 16 ).substring( 1 );
	};
	return (S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4());
};

const format = ( text, ...values ) => {
	return text.replace( /{(\d)}/g, ( _, index ) => values[ Number( index ) ] );
};

export {guidGenerator, format}
