import {format} from "../helper/string";
import {insertAfter} from "../../../editing/js/helpers/elements";

export default class Message {

	constructor() {
		this.element = this.createElement();
		this.data = {
			processed : 0,
			total : ACP_Export.total_num_items,
			download_url : '#'
		};
		this.updateData();
	}

	createElement() {
		let element = document.createElement( 'div' );
		element.classList.add( 'acp-export-notice', 'notice', 'updated' );
		element.innerHTML = TplMessage;

		return element;
	}

	setError( error ) {
		this.element.classList.add( 'error' );
		this.element.innerHTML = `<p>${error}</p>`;
		this.makeDismissible();
	}

	addProcessed( number ) {
		this.data.processed += number;
		this.updateData();
	}

	setProcessed( number ) {
		this.data.processed = number;
	}

	setTotal( number ) {
		this.data.total = number;
	}

	calcPercentage() {
		return Math.ceil( this.data.processed / this.data.total * 100 );
	}

	render() {
		document.querySelectorAll( '.wp-header-end' ).forEach( el => {
			insertAfter( this.element, el );
		} );
	}

	updateData() {
		this.element.querySelector( '.num-processed' ).innerText = this.data.processed;
		this.element.querySelector( '.total-num-items' ).innerText = this.data.total;
		this.element.querySelector( '.percentage-processed' ).innerText = this.calcPercentage();
		this.element.querySelector( '[data-download]' ).setAttribute( 'href', this.data.download_url );
	}

	setDownloadUrl( url ) {
		this.data.download_url = url;
		this.updateData();
	}

	complete() {
		this.makeDismissible();
		this.element.querySelector( '.exporting' ).style.display = 'none';
		this.element.querySelector( '.export-completed' ).style.display = 'block';
	}

	makeDismissible() {
		let button = new DismissButton();
		this.element.classList.add( 'is-dismissible' );
		this.element.appendChild( button.get() );

		button.get().addEventListener( 'click', e => {
			e.preventDefault();
			this.element.remove();
		} );
	}

}

class DismissButton {
	constructor() {
		this.element = this._create();
	}

	_create() {
		let element = document.createElement( 'button' );
		element.classList.add( 'notice-dismiss' );
		element.innerHTML = `<span class="screen-reader-text">${commonL10n.dismiss || ''}</span>`;

		return element;
	}

	get() {
		return this.element;
	}
}

const NumItemsProcessed = '<span class="num-processed"></span>';
const TotalNumItems = `<span class="total-num-items"></span>`;
const PercentageProcessed = '<span class="percentage-processed"></span>';
const TplMessage = `
	          <div class="exporting">
	            <p>
	              <span class="spinner is-active"></span>
	              ${ACP_Export.i18n.exporting}
	              ${format( ACP_Export.i18n.processed, NumItemsProcessed, TotalNumItems, PercentageProcessed )}
	            </p>
	          </div>
	          <div class="export-completed hidden">
	            <p>
	              ${format( ACP_Export.i18n.export_completed, TotalNumItems )}
	              <a href="#" class="button button-secondary" data-download>${ACP_Export.i18n.download_file}</a>
	            </p>
	          </div>
	      `;
